html {
  font: 14px/1 "Microsoft Yahei", "Hiragino Sans GB", "Helvetica Neue", Helvetica, tahoma, arial, Verdana, sans-serif, "WenQuanYi Micro Hei", "\5B8B\4F53";
  color: #ff6700;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  -webkit-backface-visibility: hidden;
}

a:active,
a:hover {
  outline: 0;
}

img {
  vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: 1em;
}

i,
em {
  font-style: normal;
}

b,
strong {
  font-weight: normal;
}

hr {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

dl,
dd {
  margin: 0;
}

input {
  outline: none;
}

input:active,
button:active {
  outline: none;
}

.clearfix {
  *zoom: 1;
}

.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.dl-layer.layui-layer .layui-layer-title {
  padding-left: 26px;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  color: #333;
  background-color: #f2f2f2;
  border-bottom: 1px solid #e5e5e5;
}

.dl-layer.layui-layer .layui-layer-btn .layui-layer-btn0 {
  margin: 0;
  width: 120px;
  height: 38px;
  line-height: 38px;
  font-size: 14px;
  color: #ffffff;
  background-color: #ff6700;
  border-color: #ff6700;
}

.dl-layer.layui-layer .layui-layer-content {
  height: auto !important;
}

.dl-layer.layui-layer .layer__content {
  text-align: center;
}

.dl-layer.layui-layer .layer__tit {
  font-size: 18px;
  color: #555;
  line-height: 30px;
}

.dl-layer.layui-layer .layer__tip-icon {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #6FACFF;
}

.dl-layer.layui-layer .layer__desc {
  text-align: left;
  font-size: 14px;
  color: #666;
}

.dl-layer.layui-layer.open-srbank-layer .layui-layer-content {
  padding: 60px 20px 30px;
}

.dl-layer.layui-layer.open-srbank-layer .layer__content .layer__tit .behavior {
  color: #ff6700;
}

.dl-layer.layui-layer.open-srbank-layer .layer__content .layer__text {
  margin-top: 7px;
  line-height: 16px;
  font-size: 12px;
  color: #8f8f8f;
}

.dl-layer.layui-layer.bind-card-layer .layui-layer-content {
  padding: 74px 0 32px;
}

.dl-layer.layui-layer.unbinding-srbank-layer .layui-layer-content {
  padding: 40px 60px 30px 64px;
}

.dl-layer.layui-layer.unbinding-srbank-layer .layer__content .layer__tit {
  line-height: 24px;
  font-size: 16px;
  margin-bottom: 10px;
}

.dl-layer.layui-layer.unbinding-srbank-layer .layer__content .layer__tit .icon-wrapper {
  display: inline-block;
  float: left;
  padding-top: 10px;
  height: 40px;
  margin-right: 10px;
}

.dl-layer.layui-layer.unbinding-srbank-layer .layer__content .layer__tit .layer__tip-icon {
  margin-right: 0;
}

.dl-layer.layui-layer.unbinding-srbank-layer .layer__content .layer__tit .text {
  display: inline-block;
  float: left;
  width: 352px;
  text-align: left;
}

.dl-layer.layui-layer.unbinding-srbank-layer .layer__content .layer__tit .layer__desc {
  padding-left: 40px;
}

.dl-layer.layui-layer.sign-srbank-layer .layui-layer-content {
  padding: 70px 30px 40px;
}

.dl-layer.layui-layer.sign-srbank-layer .layui-layer-content .text {
  font-size: 14px;
  color: #555555;
}

.dl-layer.layui-layer.sign-srbank-layer .layui-layer-content .text a {
  color: #4a9df4;
}

.dl-layer.layui-layer.save-card-layer .layui-layer-content {
  padding: 40px 54px 20px 42px;
}

.dl-layer.layui-layer.save-card-layer .layer__content .layer__tit {
  font-weight: normal;
  line-height: 1;
  margin-bottom: 18px;
}

.dl-layer.layui-layer.save-card-layer .layui-layer-btn .layui-layer-btn0,
.dl-layer.layui-layer.save-card-layer .layui-layer-btn .layui-layer-btn1 {
  width: 100px;
  height: 44px;
  line-height: 44px;
  margin: 0;
}

.dl-layer.layui-layer.save-card-layer .layui-layer-btn .layui-layer-btn0 {
  background-color: #fff;
  color: #333;
  margin-right: 7px;
}

.dl-layer.layui-layer.save-card-layer .layui-layer-btn .layui-layer-btn1 {
  margin-left: 7px;
  background-color: #ff6700;
  color: #ffffff;
  border-color: #ff6700;
}
